///

/* Banner */

	#banner {
		@include mixins.padding(10em, 0, (0, 0, functions.size(wrapper-edges, large) * -0.5, 0));

		.inner {
			margin: 0 auto;
			width: functions.size(inner);
		}

		.logo {
			@include vendor.vendor('transition', (
				'opacity 2s ease',
				'transform 1s ease',
			));
			@include vendor.vendor('transform', 'translateY(0)');
			opacity: 1;
			margin: 0 0 (functions.size(element-margin) * 0.65) 0;

			.icon {
				border-radius: 100%;
				border: solid 2px functions.palette(border);
				cursor: default;
				display: inline-block;
				font-size: 2em;
				height: 2.25em;
				line-height: 2.25em;
				text-align: center;
				width: 2.25em;
			}
		}

		h2 {
			@include vendor.vendor('transition', (
				'opacity 0.5s ease',
				'transform 0.5s ease',
				'filter 0.25s ease',
			));
			@include vendor.vendor('transform', 'translateX(0)');
			@include vendor.vendor('transition-delay', '0.65s');
			@include vendor.vendor('filter', 'blur(0)');
			opacity: 1;
			border-bottom: solid 2px functions.palette(border);
			font-size: 2.25em;
			margin-bottom: functions.size(element-margin) * 0.4;
			padding-bottom: functions.size(element-margin) * 0.2;
		}

		p {
			@include vendor.vendor('transition', (
				'opacity 0.5s ease',
				'transform 0.5s ease',
				'filter 0.25s ease',
			));
			@include vendor.vendor('transform', 'translateX(0)');
			@include vendor.vendor('transition-delay', '0.8s');
			@include vendor.vendor('filter', 'blur(0)');
			opacity: 1;
			font-family: functions.font(family-heading);
			font-size: 1em;
			font-weight: functions.font(weight-heading);
			letter-spacing: functions.font(kern-heading);
			line-height: 2;
			text-transform: uppercase;
		}

		@include breakpoints.breakpoint('<=large') {
			@include mixins.padding(7em, 0, (0, 0, functions.size(wrapper-edges, large) * 0.5, 0));

			background-color:		functions.palette(bg);

			background-image:		linear-gradient(to top, color.adjust(functions.palette(bg), $alpha: -0.2), color.adjust(functions.palette(bg), $alpha: -0.2)),
									url('../../images/bg.jpg');

			background-size:		auto,
									cover;

			background-position:	center,
									center;

		 	margin-bottom: (functions.size(wrapper-edges, large) * -1);
		}

		@include breakpoints.breakpoint('<=medium') {
			@include mixins.padding(12em, 3em, (0, 0, functions.size(wrapper-edges, medium) * 0.5, 0));

		 	margin-bottom: (functions.size(wrapper-edges, medium) * -1);

			.inner {
				width: 100%;
			}
		}

		@include breakpoints.breakpoint('<=small') {
			@include mixins.padding(5em, 2em, (0, 0, functions.size(wrapper-edges, small) * 0.5, 0));

		 	margin-bottom: (functions.size(wrapper-edges, small) * -1);

			.logo {
				margin: 0 0 (functions.size(element-margin) * 0.5) 0;

				.icon {
					font-size: 1.5em;
				}
			}

			h2 {
				font-size: 1.5em;
			}

			p {
				font-size: 0.8em;
			}
		}

		body.is-preload & {
			.logo {
				@include vendor.vendor('transform', 'translateY(0.5em)');
				opacity: 0;
			}

			h2 {
				opacity: 0;
				@include vendor.vendor('transform', 'translateX(0.25em)');
				@include vendor.vendor('filter', 'blur(2px)');
			}

			p {
				opacity: 0;
				@include vendor.vendor('transform', 'translateX(0.5em)');
				@include vendor.vendor('filter', 'blur(2px)');
			}
		}
	}