/* Footer */

	#footer {
		.inner {
			@include mixins.padding(5em, 0);
			@include vendor.vendor('display', 'flex');
			@include vendor.vendor('flex-direction', 'row');
			@include vendor.vendor('flex-wrap', 'wrap');
			margin: 0 auto;
			width: functions.size(inner);

			> * {
				width: 100%;
			}

			form {
				margin: 0 functions.size(section-spacing, large) 0 0;
				width: calc(50% - #{functions.size(section-spacing, large) * 0.5});
			}

			.contact {
				width: calc(50% - #{functions.size(section-spacing, large) * 0.5});
			}

			.copyright {
				border-top: solid 2px functions.palette(border);
				list-style: none;
				margin: (functions.size(element-margin) * 2) 0 functions.size(element-margin) 0;
				padding: functions.size(element-margin) 0 0 0;
				width: 100%;

				li {
					border-left: solid 2px functions.palette(border);
					color: functions.palette(fg-light);
					display: inline-block;
					font-size: 0.9em;
					line-height: 1;
					margin-left: 1em;
					padding: 0;
					padding-left: 1em;

					&:first-child {
						border-left: 0;
						margin-left: 0;
						padding-left: 0;
					}

					a {
						color: inherit;
					}
				}
			}
		}

		@include breakpoints.breakpoint('<=large') {

			background-color:		functions.palette(bg);

			background-image:		linear-gradient(to top, color.adjust(functions.palette(bg), $alpha: -0.2), color.adjust(functions.palette(bg), $alpha: -0.2)),
									url('../../images/bg.jpg');

			background-size:		auto,
									cover;

			background-position:	center,
									center;

		 	margin-top: (functions.size(wrapper-edges, large) * -1);
		 	padding-top: functions.size(wrapper-edges, large);

		}

		@include breakpoints.breakpoint('<=medium') {
		 	margin-top: (functions.size(wrapper-edges, medium) * -1);
		 	padding-top: functions.size(wrapper-edges, medium);

			.inner {
				@include mixins.padding(3em, 3em);
				display: block;
				width: 100%;

				form {
					width: 100%;
					margin: 0 0 (functions.size(element-margin) * 2) 0;
				}

				.contact {
					width: 100%;
					margin: 0 0 (functions.size(element-margin) * 2) 0;
				}

				.copyright {
					margin: (functions.size(element-margin) * 2) 0 functions.size(element-margin) 0;
				}
			}
		}

		@include breakpoints.breakpoint('<=small') {
		 	margin-top: (functions.size(wrapper-edges, small) * -1);
		 	padding-top: functions.size(wrapper-edges, small);

			.inner {
				@include mixins.padding(2em, 2em);

				form {
					margin: 0 0 (functions.size(element-margin) * 1.5) 0;
				}

				.contact {
					margin: 0 0 (functions.size(element-margin) * 1.5) 0;
				}
			}
		}

		@include breakpoints.breakpoint('<=xsmall') {
			.inner {
				.copyright {
					li {
						border-left: 0;
						display: block;
						margin: 1em 0 0 0;
						padding-left: 0;

						&:first-child {
							margin-top: 0;
						}
					}
				}
			}
		}

		@include breakpoints.breakpoint('<=xxsmall') {
			.inner {
				@include mixins.padding(2em, 1.5em);
			}
		}
	}