/* Button */

	input[type="submit"],
	input[type="reset"],
	input[type="button"],
	button,
	.button {
		@include vendor.vendor('appearance', 'none');
		@include vendor.vendor('transition', 'background-color #{functions.duration(transition)} ease-in-out');
		background-color: transparent;
		border-radius: functions.size(border-radius);
		border: 0;
		box-shadow: inset 0 0 0 2px functions.palette(border);
		color: functions.palette(fg-bold) !important;
		cursor: pointer;
		display: inline-block;
		font-family: functions.font(family-heading);
		font-size: 0.8em;
		font-weight: functions.font(weight-heading-bold);
		height: 3.75em;
		letter-spacing: functions.font(kern-heading);
		line-height: 3.75em;
		padding: 0 2.25em;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		white-space: nowrap;

		&:hover {
			background-color: functions.palette(border-bg);
		}

		&:active {
			background-color: functions.palette(border2-bg);
		}

		&.icon {
			&:before {
				margin-right: 0.5em;
				color: functions.palette(fg-light);
			}
		}

		&.primary {
			background-color: functions.palette(accent);
			box-shadow: none;

			&:hover {
				background-color: color.adjust(color.adjust(functions.palette(accent), $lightness: 3%), $saturation: -1.5%);
			}

			&:active {
				background-color: color.adjust(color.adjust(functions.palette(accent), $lightness: -3%), $saturation: 1.5%);
			}

			&.icon {
				&:before {
					color: color.mix(functions.palette(fg-bold), functions.palette(accent), 25%);
				}
			}
		}

		&.fit {
			width: 100%;
		}

		&.small {
			font-size: 0.6em;
		}

		&.large {
			font-size: 1em;
		}

		&.disabled,
		&:disabled {
			opacity: 0.25;
		}

		@include breakpoints.breakpoint('<=xsmall') {
			padding: 0;
		}
	}