///

/* Basic */

	// MSIE: Required for IEMobile.
		@-ms-viewport {
			width: device-width;
		}

	// MSIE: Prevents scrollbar from overlapping content.
		body {
			-ms-overflow-style: scrollbar;
		}

	// Ensures page width is always >=320px.
		@include breakpoints.breakpoint('<=xsmall') {
			html, body {
				min-width: 320px;
			}
		}

	// Set box model to border-box.
	// Based on css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice
		html {
			box-sizing: border-box;
		}

		*, *:before, *:after {
			box-sizing: inherit;
		}

	body {
		background-color:		functions.palette(bg);

		background-image:		linear-gradient(to top, color.adjust(functions.palette(bg), $alpha: -0.2), color.adjust(functions.palette(bg), $alpha: -0.2)),
								url('../../images/bg.jpg');

		background-size:		auto,
								cover;

		background-attachment:	fixed,
								fixed;

		background-position:	center,
								center;

		// Stops initial animations until page loads.
			&.is-preload {
				*, *:before, *:after {
					@include vendor.vendor('animation', 'none !important');
					@include vendor.vendor('transition', 'none !important');
				}
			}

	}