/* Features */

	.features {
		@include vendor.vendor('display', 'flex');
		@include vendor.vendor('flex-wrap', 'wrap');
		margin: 0 0 functions.size(element-margin) 0;

		article {
			@include mixins.padding(1.75em, 1.75em);
			background-color: color.adjust(color.adjust(functions.palette(bg), $lightness: 3%), $saturation: -1.5%);
			border-radius: functions.size(border-radius);
			margin: (functions.size(section-spacing, large) * 0.5) functions.size(section-spacing, large) (functions.size(section-spacing, large) * 0.5) 0;
			width: calc(50% - #{functions.size(section-spacing, large) * 0.5});

			&:nth-child(2n) {
				margin-right: 0;
			}

			.image {
				border-radius: functions.size(border-radius) functions.size(border-radius) 0 0;
				display: block;
				margin-bottom: 1.75em;
				margin-left: -1.75em;
				margin-top: -1.75em;
				position: relative;
				width: calc(100% + #{3.5em});

				img {
					border-radius: functions.size(border-radius) functions.size(border-radius) 0 0;
					width: 100%;
				}
			}
		}

		@include breakpoints.breakpoint('<=medium') {
			article {
				margin: (functions.size(section-spacing, medium) * 0.5) functions.size(section-spacing, medium) (functions.size(section-spacing, medium) * 0.5) 0;
				width: calc(50% - #{functions.size(section-spacing, medium) * 0.5});
			}
		}

		@include breakpoints.breakpoint('<=small') {
			article {
				@include mixins.padding(1.5em, 1.5em);
				margin: (functions.size(section-spacing, small) * 0.5) functions.size(section-spacing, small) (functions.size(section-spacing, small) * 0.5) 0;
				width: calc(50% - #{functions.size(section-spacing, small) * 0.5} - 1px);

				.image {
					margin-bottom: 1.5em;
					margin-left: -1.5em;
					margin-top: -1.5em;
					width: calc(100% + #{3em});
				}
			}
		}

		@include breakpoints.breakpoint('<=xsmall') {
			display: block;

			article {
				width: 100%;
				margin: 0 0 functions.size(element-margin) 0 !important;
			}
		}
	}